import "core-js/modules/es.array.push.js";
import AdminToggleDark from './dashboard/AdminToggleDark.vue';
export default {
  name: 'DashboardTabs',
  props: {
    activeTab: {
      type: String,
      default: 'dashboard'
    }
  },
  components: {
    AdminToggleDark
  },
  computed: {
    titleName() {
      if (this.activeTab === 'dashboard') {
        return '文件管理';
      } else if (this.activeTab === 'customerConfig') {
        return '用户管理';
      } else if (this.activeTab === 'systemConfig') {
        return '系统设置';
      } else {
        return '上传页面';
      }
    },
    iconName() {
      if (this.activeTab === 'dashboard') {
        return 'images';
      } else if (this.activeTab === 'customerConfig') {
        return 'user-cog';
      } else if (this.activeTab === 'systemConfig') {
        return 'cogs';
      } else {
        return 'upload';
      }
    }
  },
  methods: {
    refreshDashboard() {
      location.reload();
    },
    handleTabClick(tab) {
      this.$router.push(`/${tab}`);
    },
    handleDropdownVisible(isVisible) {
      const arrow = document.querySelector('.tabs-dropdown-link');
      if (isVisible) {
        arrow.classList.add('rotate-up'); // 添加旋转类
      } else {
        arrow.classList.remove('rotate-up'); // 移除旋转类
      }
    }
  }
};